import request from './request'

export const getAllRightsTree = () => {
  return request({
    url: 'rights/tree',
    method: 'get'
  })
}
export const getAllRightsArray = () => {
  return request({
    url: 'rights/list',
    method: 'get'
  })
}
export const editRight = (roleId, rids) => {
  return request({
    url: `roles/${roleId}/rights`,
    method: 'post',
    data: { rids: rids }
  })
}
export const removeRight = (roleId, rightId) => {
  return request({
    url: `roles/${roleId}/rights/${rightId}`,
    method: 'delete'
  })
}
